body {
  background-color: #fffaf0;
}

input {
  width: 100%;
  letter-spacing: .06em;
  color: #fff;
  text-shadow: 1px 1px 2px #000000bf;
  background-color: #c2b4a3;
  border: 1px solid #000;
  border-radius: 3px;
  margin-top: 2px;
  padding: 10px;
  font-size: 20px;
  box-shadow: 2px 2px 5px #000000bf;
}

label {
  letter-spacing: .05em;
  text-shadow: 2px 2px 3px #000000bf;
  font-size: 25px;
  font-weight: 700;
}

button {
  width: 80%;
  color: #fff;
  letter-spacing: .06em;
  text-shadow: 1px 1px 2px #000000bf;
  background-color: #c2b4a3;
  border: 1px solid #000;
  border-radius: 3px;
  padding: 8px;
  font-size: 24px;
  transition: transform .5s ease-in-out;
  box-shadow: 2px 2px 5px #000000bf;
}

button:hover {
  cursor: pointer;
  background-color: #bea280;
  transform: scale(1.05);
}

.container {
  justify-content: center;
  padding: 0 15px;
  display: flex;
}

.form {
  width: 400px;
  border: 2px solid #000;
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-top: 50px;
  padding: 30px;
  display: flex;
  box-shadow: 6px 6px 5px #000000bf;
}

